@media (min-width: 576px) {
	.header {
		&__title {
			font-size: 6rem;
		}
		&__btn {
			font-size: 7rem;
		}
	}
	.main {
		&__aboutme {
			&__box {
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 1.5fr 1fr;
				width: 70%;
				&__box {
					background-position: 0% 0%;
				}
				&__text {
					font-size: 1.8rem;
				}
			}
		}
	}
}
