@media (min-width: 996px) {
	body {
		padding: 25px;
	}

	.nav {
		&__list {
			display: flex;
		}
		&__burger {
			display: none;
		}
	}
	.header {
		&__title {
			font-size: 10rem;
			position: sticky;
		}
		&__btn {
			font-size: 7rem;
		}
	}
	.main {
		padding-top: 50px;
		padding-bottom: 50px;
		&__aboutme {
			&__box {
				min-height: 500px;
				grid-template-columns: 0.6fr 1fr;
				grid-template-rows: 1fr 1fr;
				width: 80%;
				&__img {
					grid-column: 1/2;
					grid-row: 1/3;
				}
				&__text {
					grid-column: 2/3;
					grid-row: 1/3;
					font-size: 2rem;
				}
			}
		}
		&__contact {
			&__box {
				grid-template-columns: 0.7fr 1fr;
				grid-template-rows: 1fr;
				&__describe {
					grid-column: 1/2;
					grid-row: 1/3;
				}
				&__form {
					grid-column: 2/3;
					grid-row: 1/3;
				}
			}
		}
	}
	.mobileMenu{
		display: none;
	}
}

