@media (min-width: 768px) {
	.header {
		background-position: center 0%;
		background-size: cover;
		&__title {
			font-size: 6rem;
		}
		&__btn {
			font-size: 7rem;
		}
	}
	.main {
		&__gallery {
			&__photos {
				&__photo {
					width: 50%;
					height: auto;
				}
			}
		}
		&__aboutme {
			&__box {
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 2fr 1fr;
				background-position: 100% 0%;
				width: 70%;
			}
		}
		&__contact {
			&__box {
				width: 75%;
			}
			&__text {
				padding: 1em 2em;
			}
		}
	}
}
