@import './_animations';

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}
body {
	background-color: #fff;
	min-height: 100vh;
	font-family: 'Lato', sans-serif;
	padding: 10px;
	overflow-x: hidden;
}
.nav {
	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
	top: 0;
	left: 0;
	right: 0;
	position: fixed;
	width: 100%;
	padding: 2em 1em;
	z-index: 3000;
	background-color: #fff;

	&__title {
		color: #000;
		display: flex;
		font-size: 3.5rem;
		font-weight: bolder;
		letter-spacing: 2px;
		text-decoration: none;
		cursor: pointer;
	}

	&__burger {
		font-size: 3.5rem;
		padding: 0.3em 0.6em;
		cursor: pointer;
	}
	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 1.8rem;
		text-transform: uppercase;
		display: none;
		&__element {
			padding: 0.8em 1.4em;
			margin: 0 0.25em;
			font-weight: lighter;
			letter-spacing: 2px;
			text-decoration: none;
			cursor: pointer;
			transition: 0.3s;
			color: #000;
			&:hover {
				background-color: rgb(230, 230, 230);
			}
		}
	}
}
.header {
	position: relative;
	background-image: url('../../public/img/bgHeader5.jpg');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center center;
	width: 100%;
	height: 100vh;
	font-family: 'Chivo', sans-serif;
	display: flex;
	flex-direction: column;
	// align-items: center;
	align-items: center;
	justify-content: end;
	overflow: hidden;
	z-index: 100;
	filter: grayscale(0.1);

	&__shadow {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgb(0, 0, 0);
		background: linear-gradient(180deg, rgba(0, 0, 0, 0.28335084033613445) 0%, rgba(0, 0, 0, 0.7183648459383753) 100%);
		z-index: -1;
	}

	color: #fff;
	&__title {
		font-size: 3rem;
		letter-spacing: 0px;
		text-transform: uppercase;
		animation: fromDownToUp 1s 0.3s forwards;
	}
	&__btn {
		font-size: 4rem;
		padding: 0.5em 1em;
		font-weight: lighter;
		text-transform: uppercase;
		border-radius: 15px;
		transition: 0.4s border-radius;
		color: #fffc;
		cursor: pointer;
		background-color: none;
		text-decoration: none;
		animation: 3s 1.5s goUpAndDown infinite forwards ease-in-out;
		&:hover {
			border-radius: 0px;
		}
	}
	// &::after {
	// 	content: '';
	// 	z-index: -1;
	// 	width: 600px;
	// 	height: 400px;
	// 	position: absolute;
	// 	left: 43%;
	// 	top: 23%;
	// 	background-color: rgba(221, 221, 221, 0.473);
	// 	filter: blur(1px);
	// }
}

.main {
	background-image: url(../../public//img/background2.webp);
	min-height: 100vh;
	padding-bottom: 100px;
	overflow-x: hidden;
	&__gallery {
		width: 100vw;
		min-height: 100vh;

		&__title {
			width: 200px;
		}
		&__photos {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
			&__photo {
				width: 100%;
				height: auto;
				cursor: pointer;
				// &:hover {
				// 	animation: hoverImg 0.3s forwards;
				// 	box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.4137);
				// }
				&:nth-child(3n + 2) {
					filter: grayscale(2);
				}
			}
		}
	}

	&__aboutme {
		&__title {
			width: 200px;
		}
		&__box {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1.2fr 1fr;
			display: grid;
			align-items: center;
			justify-content: center;
			margin: 0 auto;
			background-color: #fff;
			width: 80%;
			box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
			&__img {
				grid-column: 1/3;
				grid-row: 1/2;
				background-image: url(../../public/img/man.jpg);
				background-size: cover;
				background-position: 0% 0%;
				width: 100%;
				height: 100%;
			}
			&__text {
				grid-column: 1/3;
				grid-row: 2/3;

				display: flex;
				flex-direction: column;
				justify-content: flex-start;

				font-size: 1.6rem;
				padding: 0.6em 1em;

				h3 {
					font-size: 2.5rem;
					letter-spacing: 1px;
					margin: 0em 0em 1em 0;
					font-weight: bold;
				}
				p {
					margin: 0.5em 0;
				}
			}
		}
	}
	&__contact {
		&__box {
			display: grid;
			justify-content: center;
			align-items: center;
			margin: 0 auto;
			grid-template-columns: 0.7fr 1fr;
			grid-template-rows: 1fr;
			width: 90%;

			&__icons {
				display: flex;
				justify-content: start;
				flex-direction: column;
				font-size: 1.75rem;
				padding: 0.4em 0.8em;
				margin: 0.2em 0;
				cursor: pointer;

				a {
					display: flex;
					align-items: center;
					padding: 0.4em 0.2em;
					font-weight: lighter;
					text-transform: uppercase;
					i {
						font-size: 1.8rem;
						margin-right: 0.5em;
						text-decoration: none;
					}
				}
			}
			&__describe {
				grid-column: 1/3;
				grid-row: 1/2;
				display: flex;
				flex-direction: column;
				justify-content: start;
				font-size: 2.5rem;
				padding: 0.4em 1.2em;
				height: 100%;
				background-color: #000;
				color: white;
				h3 {
					text-align: center;
					margin: 0.5em 0;
				}
				p {
					margin: 0.4em 0;
				}
			}
			&__form {
				grid-column: 1/3;
				grid-row: 2/3;
				display: flex;
				justify-content: space-around;
				flex-direction: column;
				padding: 2em 4em;
				height: 100%;
				border: 1px solid black;
				background-color: #fff;

				* {
					font-size: 2rem;
					margin: 0.7em 0.5em;
					border: 1px solid black;
					padding: 0.4em 0.6em;
				}
				textarea {
					resize: vertical;
				}
				button {
					background-color: #000;
					color: white;
					cursor: pointer;
					text-transform: uppercase;
					font-size: 1.8rem;
					padding: 0.7em 0;
				}
			}
		}
	}
}

.footer {
	background-color: #000;
	color: #fff;
	h4 {
		font-size: 2.5rem;
		padding: 2em 0.5em;
		text-align: center;
	}
}

.placeHolder {
	height: 200vh;
}
.container {
	max-width: 1400px;
	margin: 0 auto;
}
.title {
	width: 200px;
	position: relative;
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: center;
	font-weight: light;
	font-size: 2rem;
	letter-spacing: 1px;
	padding: 0.2em 0;
	text-transform: uppercase;
	background-color: #333;
	color: #fff;
	margin: 5em auto;

	&::before {
		content: '';
		position: absolute;
		left: -310%;
		top: 50%;
		transform: translate(0, -50%);
		border-radius: 12px;
		width: 300%;
		height: 2px;
		background-color: #000;
	}
	&::after {
		content: '';
		position: absolute;
		right: -310%;
		top: 50%;
		transform: translate(0, -50%);
		border-radius: 12px;
		width: 300%;
		height: 2px;
		background-color: #000;
	}
}
.scaledPhoto {
	position: fixed;
	top: 0%;
	right: 0%;
	bottom: 0%;
	left: 0%;
}
.backgroundShade {
	position: absolute;
	background-color: rgba(0, 0, 0, 0.6);
	top: 0%;
	right: 0%;
	bottom: 0%;
	left: 0%;
}
.show {
	display: none;
}

.mobileMenu {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background-color: #fff;
	z-index: 10000;
	&__burger {
		position: absolute;
		top: 15px;
		right: 15px;
		font-size: 3.5rem;
		padding: 0.3em 0.5em;
	}
	&__list {
		font-size: 3.5rem;
		&__element {
			padding: 0.2em 0.4em;
			margin-top: 0.5em;
			text-decoration: none;
			color: #000;
		}
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: left;
	}
}

.showMenu {
	animation: showMobileMenu 0.35s forwards;
}
.hideMenu {
	animation: hideMobileMenu 0.35s forwards;
}
.removeDisplay {
	display: none;
}
@import './_small';
@import './_medium';
@import './_large';
@import './_xlarge';
